.section_calc {
	background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
	padding: rem(86) 0;
	margin-bottom: rem(93);

	@include min {
		padding: rem(68) 0;
	}



	&-wrap {
		margin: 0 rem(80);

		@include min {
			margin: 0;
		}

		.hide {
			display: none !important;
		}

		.success_slider-form {
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			// align-items: ;
			justify-content: center;
			// display: none;
			background-color: white;
			// position: absolute;
			// top: rem(60);
			// left: rem(92);
			// z-index: 1;
			// width: 70%;
			// height: 80%;

			.text-title {
				margin-bottom: rem(5);
			}

			.text {
				color: #373737;
				max-width: rem(292);
				margin-bottom: rem(36);
			}

			.button-green {
				width: rem(305);
			}
		}

		.success_slider-form.active {
			display: flex;
		}

		> .text-title {
			margin-bottom: rem(38);
			max-width: 70%;
		}

		&_slider {
			padding: rem(60) 0 rem(56) rem(92);
			height: rem(480);
			border-radius: rem(10);
			margin-left: rem(19);
			position: relative;
			background-color: white;
			display: flex;
			flex-direction: column;

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				right: 0;
				background: url(../img/section/section_04-01.jpg) no-repeat;
				width: rem(363);
				height: rem(368);
				background-size: cover;
				z-index: 1;
			}

			.swiper-container {
				width: 100%;
			}

			.swiper-wrapper {
				// height: auto;
			}

			.swiper-slide {
				// visibility: hidden;
				background-color: white;

				.field-set {
					width: rem(295);
				}
			}

			.swiper-slide-active {
				// visibility: visible;
			}

			.swiper-container-horizontal>.swiper-pagination-bullets,
			.swiper-pagination-custom,
			.swiper-pagination-fraction {
				width: auto;
			}

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: rem(-19);
				width: rem(2);
				height: calc(100% - 24px);
				background-color: white;
				border-radius: rem(3);
			}

			.nav {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(22);

				button {

					i {
						font-size: rem(14);
					}

					&:first-of-type {
						margin-right: rem(8);
					}
					&:last-of-type {
						margin-left: rem(8);
					}


				}

				&_control {
					font-size: rem(14);
					color: #373737;
				}
			}

			.slide_header {
				margin-bottom: rem(24);

				span {
					color: #373737;
					font-size: rem(24);
					font-weight: 600;
				}
			}

			.slide_wrap {
				margin-bottom: rem(74);
				label {
					margin-bottom: rem(14);

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			.slide_button {

				display: flex;
				flex-wrap: wrap;

				.button-green {
					width: rem(214);
				}

				.button-prev {
					color: #A0A0A0;
					text-transform: uppercase;
					font-weight: 600;
					margin-right: rem(20);
				}
			}
		}
	}
}