.header {
	position: sticky;
	top: 0;
	background-color: #F9F9FF;
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.06);
	padding-bottom: rem(20);
	width: 100%;
	z-index: 100;

	@include min768 {
		background-color: #F0F7FF;
		box-shadow: none;
		padding-bottom: 0;
	}

	&_top {
		padding-top: rem(13);
		padding-bottom: rem(8);
		border-bottom: rem(1) solid #DDDFEC;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min768 {
			align-items: center;
			border-bottom: 0;
			// padding-bottom: 0;
		}
	}

	&_burger {
		display: none;

		cursor: pointer;
		height: rem(18);
		width: rem(27);

		span {
			width: 100%;
			height: rem(2);
			background-color: #2F2F2F;
			border-radius: rem(6);
		}

		&::before, &::after {
			content: "";
			width: 100%;
			height: rem(2);
			background-color: #2F2F2F;
			border-radius: rem(6);
		}

		@include min768 {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
		}
	}
	

	&_logo {
		margin-right: auto;

		a {
			display: block;
		}

		@include min {
			max-width: rem(168);
			align-items: center;
			display: flex;

			img {
				// width: 100%;
				// height: 100%;
				// object-fit: cover;
			}
		}

		@include min768 {
			max-width: rem(136);
		}
	}

	&_control {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		@include min768 {
			display: none;
		}

		&-first {
			margin-right: rem(20);

			i {
				font-size: rem(24);
			}
		}

		&-buttons {
			margin-right: rem(25);
			display: flex;
			flex-wrap: wrap;

			a {
				&:first-child {
					margin-right: rem(14);
				}
				
				@include min {
					max-width: rem(133);
					padding: rem(4) rem(12) rem(7) rem(12);
					line-height: rem(14);
					&::after {
						background-color: transparent;
					}
				}
			}
		}

		&-list {
			margin-right: rem(24);
			// flex: 1 0 0;
			// display: flex;
			// flex-wrap: wrap;
			// flex-direction: column;
			// justify-content: center;
			// max-height: rem(44);

			@include min {
				margin-right: 0;
			}

			.smart {
				display: none;

				@include min {
					display: block;
				}
			}

			a {
				display: block;
				font-size: rem(18);
				color: #2F2F2F;
				margin-right: rem(24);

				@include min {
					font-size: rem(16);
				}

				// &:last-child {
				// 	display: inline-block;
				// 	// flex: 1 0 0;
				// 	margin-right: 0;
				// }
			}
		}

		&-last {
			a {
				font-size: rem(18);
				color: #2F2F2F;
			}

			@include min {
				display: none;
			}
		}
	}
}

.menu {
	padding-top: rem(13);
	display: flex;
	justify-content: center;
	
	@include min768 {
		display: none;
	}

	> ul {
		display: flex;
		flex-wrap: wrap;
		
		@include min {
			width: 100%;
			justify-content: space-between;
		}

		> li {
			
			position: relative;

			@include min {
				&:first-child {
					a {
						padding-left: 0;
					}
					
				}

				&:last-child {
					a {
						margin-right: 0;
					}
					
				}
			}
			

			&::after {
				content: "";
				position: absolute;
				top: 0;
				right: 0;
				width: rem(1);
				height: 100%;
				background-color: #DDDFEC;
			}

			> a {
				font-size: rem(18);
				padding: 0 rem(39);
				transition: all 200ms ease;

				@include min {
					font-size: rem(16);
					padding: 0 rem(14);

					
				}

				&:hover {
					color: $green-text;
				}
			}

			&:last-child {
				margin-right: 0;

				&::after {
					display: none;
				}
			}
		}
		li.active {
			// background-color: white;
			position: relative;
			> a {
				position: relative;
				z-index: 1;
				font-weight: 600;
				color: $green-text;
			}

			&::before {
				content: "";
				position: absolute;
				top: rem(-13);
				left: 0;
				width: calc(100% - 1px);
				height: rem(60);
				background-color: white;
				z-index: 1;
			}

			.menu-panel {
				display: block;
			}
		}
	}
}

.menu-panel {
	position: absolute;
	top: rem(40);
	left: 0;
	min-width: rem(365);
	background-color: white;
	box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.07);
	padding: rem(16) rem(32);
	display: none;

	span {
		font-size: rem(18);
		font-weight: 600;
		margin-bottom: rem(8);
		display: block;
	}

	ul {
		margin-bottom: rem(12);
		&:last-child {
			margin-bottom: 0;
		}
		li {
			position: relative;
			margin-bottom: rem(6);
			padding-left: rem(20);

			&::before {
				content: "-";
				position: absolute;
				top: 0;
				left: 0;
				font-weight: 600;
				color: $green-text;
			}

			&:last-child {
				margin-bottom: 0;
			}
			a {
				font-size: rem(18);
				white-space: nowrap;

				&:hover {
					color: $green-text;
				}
			}
		}
	}
}