.menu_smart {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: white;

	display: flex;
	flex-direction: column;

	z-index: 100;
	overflow-y: auto;

	padding: rem(14) rem(20) rem(40) rem(20);
	display: none;

	@include max768 {
		display: none;
	}

	&-close {
		width: rem(27);
		height: rem(27);
		display: block;

		i {
			font-size: rem(24);
		}
	}

	&-top {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(20);

		&_logo {
			max-width: rem(137);
		}

		.button-b-icon {
			width: rem(150);
		}

		.flex:first-of-type {
			margin-bottom: rem(20);
		}
	}

	&-nav {

		> ul {
			> li {
				padding: rem(10) 0;
				border-bottom: rem(1) solid #BEBBD2;

				&:last-child {
					border-bottom: 0;
				}

				> a {
					font-weight: 600;
					position: relative;
					display: flex;
					align-items: center;

					.arrow {
						// position: absolute;
						// top: rem(4);
						// left: rig
						margin-left: rem(7);
						width: 0;
						height: 0;
						border-top: rem(7) solid transparent;
						border-left: rem(14) solid #C4C4C4;
						border-bottom: rem(7) solid transparent
					}
				}

				

				.menu-panel_smart {
					display: none;

					margin-top: rem(8);

					span {
						font-size: rem(18);
						font-weight: 600;
						margin-bottom: rem(8);
						display: block;
					}

					ul {
						margin-bottom: rem(12);

						&:last-child {
							margin-bottom: 0;
						}

						li {
							position: relative;
							margin-bottom: rem(6);
							padding-left: rem(20);

							&::before {
								content: "-";
								position: absolute;
								top: 0;
								left: 0;
								font-weight: 600;
								color: $green-text;
							}

							&:last-child {
								margin-bottom: 0;
							}

							a {
								font-size: rem(18);
								white-space: nowrap;

								&:hover {
									color: $green-text;
								}
							}
						}
					}
				}
			}

			> li.active {
				.menu-panel_smart {
					display: block;
				}
			}
		}
	}

	&-bottom {
		margin-top: auto;

		a {
			display: block;
			color: #2F2F2F;
			font-size: rem(16);

			&:nth-child(2) {
				margin-bottom: rem(10);
			}

			&:last-child {
				margin-top: rem(36);

				i {
					font-size: rem(24);
					color: black;
				}
			}
		}
	}
}

.menu_smart.active {
	display: flex;
}