* {
	outline: none;
	border: 0;
}

html {
	font-size: 100%;
	height: 100%;

	@include max {
		font-size: 140%;
	}
	@include hd {
		font-size: 180%;
	}
	@include w1200_w900 {
		font-size: 75%;
	}
	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: 'Rubik';
	font-weight: 400;
	font-size: rem(20);
	// height: 100%;
	overflow-x: hidden;
}

.relative {
	position: relative;
}
.container-fluid {
	position: relative;
	width: 100%;
	margin: 0 auto;
}
.container {
	width: 1240px;
	margin: 0 auto;

	// @include max {
	// 	width: 1300px;
	// }

	@include w1200_w900 {
		width: 900px;
	}

	@include min {
		width: calc(100% - 40px);
	}

	@include max {
		width: 1600px;
	}

	@include hd {
		width: 2400px;
	}
	@include w1200_w900 {
		width: 900px;
	}
}

.grid-col-right {
	display: grid;
	grid-template-columns: calc((100% - 1240px)/2) 4.3fr 5.7fr calc((100% - 1240px)/2);

	@include max {
		grid-template-columns: calc((100% - 1600px)/2) 4.3fr 5.7fr calc((100% - 1600px)/2);
	}

	@include hd {
		grid-template-columns: calc((100% - 2400px)/2) 4.3fr 5.7fr calc((100% - 2400px)/2);
	}

	@include w1200_w900 {
		grid-template-columns: calc((100% - 900px)/2) 4.3fr 5.7fr calc((100% - 900px)/2);
	}

	@include min {
		grid-template-columns: calc((100% - 900px)/2) 5fr 5fr calc((100% - 900px)/2);
	}

	
}


.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.button-b-icon {
	position: relative;
	border-radius: rem(49);
	padding: rem(6) rem(20) rem(8) rem(20);
	background: $green;
	font-size: rem(14);
	display: flex;
	align-items: center;
	color: black;
	// background-clip: padding-box;
	border: rem(2) solid transparent;
	transition: all 400ms ease;
	// z-index: 0;

	&::after {
		content: "";
		width: calc(100% - 2px);
		height: calc(100% - 2px);
		position: absolute;
		top: 1px;
		bottom: 1px;
		left: 1px;
		right: 1px;
		background-color: white;
		border-radius: rem(49);
		z-index: 0;
		transition: all 200ms ease;
	}

	&:hover {
		&::after {
			background-color: transparent;
		}
	}
	
	i,span {
		position: relative;
		z-index: 2;
	}

	i {
		margin-right: rem(6);
		font-size: rem(22);
		color: black;
	}
}

.button-green {
	border-radius: rem(49);
	background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
	padding: rem(18) rem(57);
	text-transform: uppercase;
	font-weight: 600;
	color: #1C3E1F;
	display: inline-block;
	// border: 3px solid $green-text;
	transition: all 400ms ease;

	&:hover {
		background-color: white;
	}

	@include min768 {
		width: 100%;
		text-align: center;
	}
}

.button-white {
	border-radius: rem(49);
	background-color: white;
	padding: rem(18) rem(57);
	text-transform: uppercase;
	font-weight: 600;
	color: #1C3E1F;
	display: inline-block;
	border: 3px solid white;
	transition: all 400ms ease;

	@include min768 {
		width: 100%;
		padding: rem(12) 0;
		text-align: center;
	}

	// &:hover {
	// 	background-color: white;
	// }
}

.elipse {
	width: rem(666);
	height: rem(666);
	border-radius: 50%;
	background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
	position: absolute;
	top: 50%;
	margin-top: rem(-200);
	right: 0;
	margin-right: rem(-433);
	z-index: -1;

	@include min {
		width: rem(416);
		height: rem(416);
		margin-right: rem(-280);
		margin-top: rem(-120);
	}

	@include min768 {
		width: rem(276);
		height: rem(276);
		margin-right: rem(-200);
		margin-top: rem(50);
	}
}

.elipse-small {
	width: rem(291);
	height: rem(291);
	border-radius: 50%;
	background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
	position: absolute;
	top: 100%;
	margin-top: rem(-100);
	left: 0;
	margin-left: rem(-250);
	z-index: 1;

	@include min {
		// margin-right: rem(-280);
		// margin-top: rem(-120);
	}
}

.z-2 {
	z-index: 2;
}

