.field-checked {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	cursor: pointer;

	input[type="radio"], input[type="checkbox"] {
		display: none;

		&:checked ~ .check::before {
			opacity: 1;
		}
	}

	input[type="radio"]~.check {
		border-radius: 50%;
		width: rem(16);
		height: rem(16);
		border: rem(1) solid #BFBFBF;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		margin-right: rem(7);

		&::before {
			content: "";
			border-radius: 50%;
			width: rem(8);
			height: rem(8);
			background-color: $green-text;
			opacity: 0;
			transition: all 400ms ease;
		}
	}

	input[type="checkbox"]~.check {
		width: rem(16);
		height: rem(16);
		border: rem(1) solid #BFBFBF;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		margin-right: rem(7);

		&::before {
			content: "";
			width: rem(8);
			height: rem(8);
			background-color: $green-text;
			opacity: 0;
			transition: all 400ms ease;
		}
	}

	

	.text {
		color: #373737;
	}
}

.field-set {
	margin-bottom: rem(22);

	&:last-of-type {
		margin-bottom: 0;
	}
	.label {
		color: #373737;
		margin-bottom: rem(3);
		display: block;
	}
	
	input, textarea {
		border-radius: rem(4);
		height: rem(44);
		width: rem(295);
		border: rem(1) solid #B7C0E1;
		padding: rem(10) rem(20);
		color: #373737;
		font-size: rem(20);

		@include placeholder{
			color: #9FA3B2;
		}
	}
}

@include min {
	.sm-pb-130 {
		padding-bottom: rem(130) !important;
	}
}
@include min768 {
	.sm-pb-130 {
		padding-bottom: rem(36) !important;
	}
}

.swiper-pagination-current:before {
	content: "0"
}

.swiper-pagination-total:before {
	content: "0"
}

.scroll_hidden {
	overflow: hidden;
}