.section_contact {
	background-color: #F0F7FF;
	padding-bottom: rem(100);
	padding-top: rem(68);

	.text-title {
		margin-bottom: rem(22);
	}

	&-left {
		width: 42%;

		.text {
			color: #373737;
			margin-bottom: rem(30);
			max-width: rem(380);
		}

		.contact-list {
			display: flex;
			flex-direction: column;

			&_item {
				margin-bottom: rem(22);
				display: flex;
				align-items: center;
				color: #373737;

				i {
					font-size: rem(32);
					margin-right: rem(15);
				}

				&:last-child {
					margin-bottom: 0;
				}
			}

			&_item.flex-start {
				align-items: flex-start;

				a {
					line-height: 138%;
				}
			}
		}
	}

	&-right {
		width: 58%;

		.button-green {
			font-size: rem(20);
		}
	}
}

.field-set {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.field {
		width: calc(50% - 0.625rem);
	}
}

.field {
	// label {
	// 	display: block;
	// 	color: #373737;
	// 	margin-bottom: rem(7);
	// }

	input[type="text"], input[type="email"], textarea {
		// border-radius: rem(4);
		// height: rem(44);
		width: 100%;
		// border: rem(1) solid #B7C0E1;
	}
	
}

.field-set {
	textarea {
		width: 100% !important;
		height: rem(125) !important;
		max-height: rem(125);
		resize: none;
	}
}

.field-control {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	align-items: center;
}

.field-upload {
	margin-right: rem(34);
	input {
		display: none;
	}

	label {
		cursor: pointer;
		display: flex;
		align-items: center;

		.paper-clip {
			width: rem(23);
			height: rem(23);
			background-size: rem(23);
			display: block;
			margin-right: rem(7);
			background: url(../img/paper-clip.svg) no-repeat;
		}
		span {
			font-size: rem(14);
			text-decoration: underline;
		}
	}
}