.item_inset {
	position: relative;
	overflow: hidden;
	border-radius: rem(10);
	display: block;

	@include min768 {
		height: rem(148);
		margin-bottom: rem(20);
	}

	&-image {
		background-size: cover;
		width: 100%;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&-text {
		position: absolute;
		bottom: rem(26);
		left: rem(31);
		z-index: 1;
		max-width: rem(232);

		@include min {
			bottom: rem(18);
			left: rem(18);
		}

		.title {
			color: white;
			font-weight: 600;
			line-height: 93%;
		}
	}
}

@include min768 {
	.smart-block {
		.w-6\/12 {
			width: 100%;
		}
		.w-4\/12 {
			width: 100%;
		}

		.mb-25 {
			margin-bottom: 0;
		}
	}
}

