.header_start {
	position: relative;
	// display: flex;
	// flex-wrap: wrap;
	margin-bottom: rem(120);
	// max-height: rem(535);
	// overflow: hidden;
	background-color: #F0F7FF;

	.start_left-smart {
		display: none !important;
	}

	.start_left-control {
		position: absolute;
		left: 0;
		bottom: rem(25);
		margin-top: rem(57);
		display: flex;
		// flex-wrap: wrap;
		align-items: center;
		width: 100%;
		z-index: 10;

		@include min768 {
			position: relative;
			left: auto;
			bottom: auto;
			margin-top: rem(24);
			padding: 0 rem(20);
		}

		.swiper-pagination-fraction {
			width: auto;
		}

		button {
			&:first-child {
				margin-right: rem(8);
			}

			&:last-child {
				margin-left: rem(8);
			}
		}

		.count_slider {
			display: flex;
			font-size: rem(14);
			color: #373737;

			span {
				font-size: rem(14);
			}
		}
	}

	@include min {
		padding-left: rem(20);
		height: rem(465);

		margin-bottom: rem(85);
	}

	@include min768 {
		padding-left: 0;
		margin-bottom: rem(30);
		height: auto;
		display: flex !important;
		flex-wrap: wrap;

		.start_left-smart {
			display: flex !important;
		}

		.swiper-container {
			padding: 0 rem(20);
		}
	}
	

	&-left,
	#slider_header {
		margin-top: rem(26);
		
		position: relative;

		@include max768 {
			grid-column: 2 / span 1;
		}

		@include min768 {
			margin-top: rem(16);
		}
		

		>.descr {
			max-width: rem(500);
			margin-bottom: rem(26);
			padding-bottom: rem(100);

			@include min {
				max-width: rem(326);
				margin-bottom: rem(20);
				padding-bottom: rem(60);
			}
		}

		.title {
			font-size: rem(64);
			font-weight: 600;
			color: #121212;
			margin-bottom: rem(30);
			white-space: nowrap;
			position: absolute;
			top: rem(65);
			left: 0;

			@include min {
				top: rem(54);
				font-size: rem(44);
			}

			@include min768 {
				font-size: rem(34);
				// white-space: normal;
			}
		}

		.start_left-text {
			padding-left: rem(82);

			@include min {
				padding-left: rem(40);
			}

			@include min768 {
				padding-left: 0;
			}

			.descr {
				padding-left: rem(20);
				position: relative;
				max-width: rem(410);
				margin-bottom: rem(34);

				@include min {
					max-width: rem(265);
				}

				@include min768 {
					margin-left: rem(40);
				}

				&::before {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					height: 100%;
					width: rem(2);
					border-radius: rem(2);
					background-color: $green-text;
				}
			}
		}


		
	}

	&-right {
		// position: absolute;
		// top: 0;
		// right: -20%;
		// z-index: -1;
		// width: 60%;
		background-size: cover;
		
		display: flex;
		overflow: hidden;

		@include max768 {
			grid-column-end: span 2;
		}

		@include min768 {
			width: 100%;
			height: rem(268);
			margin-top: rem(28);
		}

		@include min {
			position: relative;
		}
		
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			// display: none;
			transition: all 400ms ease;

			@include min {
				position: absolute;
				top: 0;
				left: 0;
			}

			&:first-child {
				order: -9999;
			}
		}
	}
}