.item_line {
	padding-right: rem(40);

	@include min768 {
		padding-right: rem(20);
	}

	&-top {
		margin-bottom: rem(20);
		position: relative;
		height: rem(65);

		.icon-arrow-line {
			font-size: rem(14);
			position: absolute;
			top: rem(32);
			right: 0;

			@include min {
				font-size: rem(9);
				right: rem(-30);
			}

			@include min768 {
				display: none;
			}
		}

		&_count {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: rem(65);
			&::before {
				content: attr(data-count);
				position: absolute;
				left: 0;
				bottom: 0;
				font-weight: 700;
				font-size: rem(70);
				line-height: rem(50);
				color: $green-text;
				background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				z-index: -1;
			}

			i {
				font-size: rem(65);
				position: absolute;
				bottom: 0;
				left: 0;

				@include min {
					font-size: rem(54);
				}
			}
		}
	}

	.text {
		color: #808080;

		@include min {
			font-size: rem(16);
		}
	}
}
.item_1 {
	.item_line-top_count i {
		left: rem(17);
	}
}
.item_2 {
	.item_line-top_count i {
		left: rem(24);
		bottom: rem(-2);
	}
}
.item_3 {
	.item_line-top_count i {
		left: rem(20);
		bottom: rem(1);
		// bottom: rem(-2);
	}
}
.item_4 {
	padding-right: 0;
	.item_line-top_count i {
		left: rem(34);
		bottom: rem(-10);
		// bottom: rem(-2);
	}
}