.advantages-wrap {
	background-color: #F0F7FF;
	width: 100%;
	padding-top: rem(90);
	position: relative;

	@include min {
		padding-top: rem(64);
	}
}

.section-advantages {

	.text-center {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.text-title {
		margin-bottom: rem(18);
	}

	.descr {
		padding: rem(14) 0;
		max-width: rem(850);
		border-top: rem(2) solid $green-text;
		border-bottom: rem(2) solid $green-text;
		margin-bottom: rem(60);
	}

	.swiper-wrapper {
		

		@include max768 {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}
	}

	@include min768 {
		.w-6\/12 {
			width: 100%;
		}
	}
}

.item_adv {
	position: relative;
	margin-bottom: rem(73);

	@include min768 {
		margin-bottom: rem(24);
	}

	&-icon {
		position: absolute;
		top: 0;
		left: 0;

		&::after {
			content: "";
			position: absolute;
			bottom: -25%;
			right: -25%;
			z-index: -1;
			width: rem(52);
			height: rem(52);
			border-radius: 50%;
			background-color: $green-text;
		}

		i {
			font-size: rem(64);

			@include min {
				font-size: rem(50);
			}
		}
	}

	&-text {
		position: relative;
		z-index: 2;
		padding-left: rem(100);

		@include min {
			padding-left: rem(60);
		}

		.title {
			color: #373737;
			font-size: rem(24);
			font-weight: 600;
			margin-bottom: rem(20);
			display: block;

			@include min {
				font-size: rem(20);
				margin-bottom: rem(10);
			}
		}
		.text {
			color: #808080;
			max-width: rem(458);

			@include min {
				font-size: rem(16);
			}
		}
	}
}