.section_reviews {
	position: relative;

	@include min768 {
		>.swiper-nav {
			display: none;
		}
	}

	.text-title {
		margin-bottom: rem(28);
	}

	.button-green {
		text-align: center;
	}

	.item_review {
		padding: rem(40) rem(45);
		border-radius: rem(10);
		overflow: hidden;
		background-color: #F0F7FF;
		display: flex;
		flex-wrap: wrap;

		@include min {
			padding: rem(20);

		}

		&-image {
			border-radius: 50%;
			width: rem(150);
			height: rem(150);
			overflow: hidden;
			margin-right: rem(30);

			@include min {
				width: rem(60);
				height: rem(60);
				margin-right: rem(16);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&-text {
			flex: 1 0 0;
			.title {
				margin-bottom: rem(10);
				display: block;
				font-weight: 500;
				font-size: rem(24);

				@include min {
					font-size: rem(20);
				}
			}
			.descr {
				max-width: rem(320);
			}
		}
	}
}

.slider_nav-smart {
	display: none;

	@include min768 {
		display: flex;
		justify-content: center;
		margin-bottom: rem(12);
		height: rem(40);

		.swiper-nav {
			position: relative;
			top: auto;
			right: auto;
			bottom: auto;
			left: auto;
		}
	}
}