.section_work {
	position: relative;
	margin-bottom: rem(155);

	.container {
		position: relative;

		> .swiper-nav {
			@include min768 {
				display: none;
			}
		}
	}

	.swiper-nav {
		bottom: rem(-300);
		right: 0;

		
	}
	.text-title {
		margin-bottom: rem(40);
	}

	.page {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		border-bottom: rem(2) solid #CED8D4;
		margin-bottom: rem(73);

		@include min {
			flex-wrap: nowrap;
		}

		&_item {
			position: relative;
			cursor: pointer;
			font-weight: 700;
			font-size: rem(18);
			color: #373737;
			text-transform: uppercase;
			padding: 0 rem(10) rem(14) rem(10);

			@include min {
				white-space: nowrap;
			}

			&:hover {
				color: $green-text;

				&::after {
					content: "";
					position: absolute;
					bottom: rem(-3);
					left: 0;
					height: rem(4);
					width: 100%;
					background-color: $green-text;
				}
			}
		}
		&_item.active {
			color: $green-text;

			&::after {
				content: "";
				position: absolute;
				bottom: rem(-3);
				left: 0;
				height: rem(4);
				width: 100%;
				background-color: $green-text;
			}
		}
	}

	&-slider {
		display: flex;
		align-items: center;
		justify-content: space-between;

		.swiper-wrapper {
			align-items: center;
			
			height: rem(482);

			@include min {
				height: auto;
			}
		}

		.swiper-slide {
			border-radius: rem(10);
			overflow: hidden;
			background-size: cover;
			width: rem(417) !important;
			height: rem(334) !important;
			transition: all 400ms ease;

			@include min {
				// width: 25% !important;
				// height: rem(167) !important;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.swiper-slide-active {
			height: rem(482) !important;
			width: rem(605) !important;

			@include min {
				// height: rem(284) !important;
				// width: 50% !important;

				img {
					height: rem(284) !important;
					width: 100% !important;
				}
			}
		}
	}
}

.swiper-button-prev,
.swiper-button-next {

	button {
		font-size: rem(28);
		color: #898989;
	}
}

.swiper-nav {
	position: absolute;
	top: rem(10);
	right: 0;
	width: rem(85);
	display: flex;
	justify-content: space-between;
}