.item_outer {
	position: relative;
	overflow: hidden;
	border-radius: rem(10);
	display: block;

	&-image {
		background-size: cover;
		width: 100%;
		border-radius: rem(10);
		overflow: hidden;

		@include min768 {
			height: rem(100);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&-text {
		max-width: rem(232);
		margin-top: rem(11);

		.title {
			color: #373737;
			font-weight: 600;
		}
	}
}

#slider_section3 {

	@include min768 {
		width: 100%;
		margin-bottom: 0;

		.slider_nav-smart {
			margin-top: rem(32);
		}
	}

	@include max768 {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.swiper-wrapper {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		.item-t-50 {
			margin-top: rem(50);
		}
	}
	
}