.social-section {
	position: relative;
	overflow: hidden;
	width: 100%;
	margin: 0 auto;
	height: rem(340);
	display: flex;
	flex-wrap: wrap;
	// padding-bottom: rem(90);
	// background-color: $green-text;
	// display: grid;
	// grid-template-columns: calc((100% - 1240px)/2) 7fr 3fr calc((100% - 1240px)/2);

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 40%;
		background-color: #05E179;
		z-index: -1;
	}

	@include min768 {
		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			height: 100%;
			width: 40%;
			background-color: #05E179;
			z-index: -1;
		}
	}

	&_text {
		position: relative;
		
		background-color: #05E179;
		width: 64%;
		height: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		padding-left: rem(80);
		padding-top: rem(56);
		padding-bottom: rem(68);
		// border-top-right-radius: 25%;
		// grid-column: 2 / span 1;

		@include min {
			padding-left: 0;
			padding-top: rem(35);
			padding-bottom: rem(28);
			width: 65%;
		}

		@include min768 {
			width: 100%;
		}

		&::after {
			content: "";
			position: absolute;
			top: -5%;
			right: -17%;
			width: rem(388);
			height: rem(388);
			border-radius: 50%;
			background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
			transform: rotate(-180deg);
			z-index: 1;

			@include min {
				top: -12%;
				height: rem(420);
			}

			@include min768 {
				display: none;
			}
		}

		.text-title {
			position: relative;
			z-index: 2;
			display: block;
			width: 100%;
			max-width: rem(600);
			margin-bottom: rem(38);

			@include min {
				margin-bottom: rem(18);
				max-width: rem(490);
			}
		}

		.control {
			position: relative;
			z-index: 2;
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min {
				padding-left: rem(40);
			}

			@include min768 {
				padding-left: 0;
			}

			.descr {
				max-width: rem(390);
				padding-left: rem(20);
				margin-right: rem(22);
				border-left: rem(2) solid white;

				@include min {
					margin-bottom: rem(32);
					margin-left: rem(24);
				}

				@include min768 {
					max-width: 100%;
					margin-right: 0;
					padding-left: rem(10);
				}
			}
		}
	}
	&_right {
		position: absolute;
		top: 0;
		right: 0;
		width: 36%;
		height: 100%;
		// position: absolute;
		// top: 0;
		// right: 0;
		// grid-column-end: span 2;

		@include min {
			width: 54%;
		}

		@include min768 {
			display: none;
		}

		img {
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}