.footer {
	background-color: #EAF4FF;
	padding-top: rem(29);
	padding-bottom: rem(36);

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	&_logo {
		margin-right: rem(46);
	}

	&_nav {

		ul {
			display: flex;
			flex-wrap: wrap;

			li {
				margin-right: rem(40);

				&:last-child {
					margin-right: 0;
				}

				a {
					font-size: rem(14);
					color: #3F3F3F;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}
}